html {
  --spec-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.logo_link {
  font-size: 0;
  text-decoration: none;
}

.Logo_img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav_item {
  list-style: none;
}

.nav_list :not(:first-child) {
  margin-left: 20px;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--spec-color);
  position: relative;
}

.nav_link:hover {
  color: var(--spec-color);
}

.is-active:after {
  content: "";
  background-color: var(--spec-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

[data-qa="hover"] {
  color: var(--spec-color);
}

